	save()
	{ 
		if(this.state.options[0]===false && this.state.options[1]===false && this.state.options[2]===false && this.state.options[3]===false )
			{ this.setState(update(this.state, {
	marked: {
		[this.state.Questionno]: {
			$set: -1
		}
	}
}));
			}
		else {
	this.setState(update(this.state, {
	buttoncolor: {
		[this.state.Questionmark]: {
			$set: 'green'
		}
	}
}));
	localStorage.setItem('datab',this.state.buttoncolor);
	
}
	const loginRef = firebase.database().ref('marked/');
    loginRef.update(this.state.marked)
	
	this.setState({Questionno:this.state.Questionno+1})
	this.setState({Questionmark:this.state.Questionmark+1})
	if(this.state.marked[this.state.Questionmark]===0)
	{
		this.setState({options:[true,false,false,false]})}
	else if (this.state.marked[this.state.Questionmark]===1)
		{this.setState({options:[false,true,false,false]})}
	else if (this.state.marked[this.state.Questionmark]===2)
		{this.setState({options:[false,false,true,false]})}
	else if (this.state.marked[this.state.Questionmark]===3)
		{this.setState({options:[false,false,false,true]} )}
	else 
		{this.setState({options:[false,false,false,false]})
}

	


}
 componentDidMount() {
        this.myInterval = setInterval(() => {
            const { seconds, minutes, hours} = this.state

            if (seconds > 0) {
                this.setState(({ seconds }) => ({
                    seconds: seconds - 1
                }))
            }

            if (minutes===0 && seconds===0 && hours!==0)
            {
            	this.setState(({ hours }) => ({
                        hours: hours - 1,
                        minutes: 59}))
            }
            
                if (seconds===0)
                  {
                    this.setState(({ minutes }) => ({
                        minutes: minutes - 1,
                        seconds: 59
                    }))
                
            }
        }, 1000)
    const loginRef = firebase.database().ref(this.props.location.state.username)
    loginRef.on('value', snapshot => {
    const data = snapshot.val()
    this.setState ({marked : data})})
    this.setState({username : this.props.location.state.username})
        
    }

	

	click(e)
	{ this.setState({Questionmark:e})
	this.setState({Questionno:e-1})  
	if(this.state.marked[e-1]===0)
	{  
		this.setState({options:[true,false,false,false]})}
	else if (this.state.marked[e-1]===1)
		{this.setState({options:[false,true,false,false]})}
	else if (this.state.marked[e-1]===2)
		{this.setState({options:[false,false,true,false]})}
	else if (this.state.marked[e-1]===3)
		{this.setState({options:[false,false,false,true]} )}
	else 
		{this.setState({options:[false,false,false,false]})
}

}

